import React, { useState } from 'react';
import axios from 'axios';
import PropTypes from 'prop-types';
import Head from '../../components/Head/Head';
import Videos from '../../components/Videos/Videos';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import {
	faEnvelope,
	faUserCircle,
	faVolumeUp,
} from '@fortawesome/free-solid-svg-icons';
import styles from './Registration.module.scss';
import Container from '../../components/Containers/Container';
import ModalContainer from '../../components/ModalContainer/ModalContainer';
import MyInput from '../../components/MyInput/MyInput';
import Button from '../../components/Button/Button';
import { Link, useHistory } from 'react-router-dom';
import BrandBar from '../../components/BrandBar/BrandBar';
import Steps from '../../components/Steps/Steps';
import StaticMainText from '../../components/StaticMainText/StaticMainText';
import Slider from '../../components/Slider/Slider';
import TextSection from '../../components/TextSection/TextSection';
import Footer from '../../components/Footer/Footer';
import MyFormRegistraion from '../../components/MyForm/MyFormRegistraion';
const REACT_APP_API = 'http://localhost:1992/react-contact-form/index.php';

const Registration = props => {
	const history = useHistory();
	const [sent, setSent] = useState(false);
	const [error, setError] = useState(null);
	function sendEmail(data) {
		axios({
			method: 'post',
			url: `/api/forma`,
			headers: { 'content-type': 'application/json' },
			data: data,
		}).then(res => {
			setSent(true);
		});
	}

	const goToBack = () => {
		history.push('/thanks');
	};
	return (
		<div>
			<div className={styles.container__reg}>
				<Head />
				<StaticMainText />
				<Container>
					<section className={styles.container__video_reg}>
						<Videos FontAwesomeIcon={FontAwesomeIcon} faVolumeUp={faVolumeUp} />
						<MyFormRegistraion
							sendEmail={sendEmail}
							sent={sent}
							goToBack={goToBack}
							children={
								<>
									<div className={styles.container__input}>
										<MyInput
											type="text"
											name="name"
											widthBox="50%"
											icon={
												<>
													<FontAwesomeIcon icon={faUserCircle} />{' '}
												</>
											}
											placeholder="Ваше имя"
										/>
										<MyInput
											type="text"
											name="lastName"
											widthBox="50%"
											icon={
												<>
													<FontAwesomeIcon icon={faUserCircle} />{' '}
												</>
											}
											placeholder="Ваша фамилия"
										/>
										<MyInput
											type="email"
											name="email"
											widthBox="50%"
											icon={
												<>
													<FontAwesomeIcon icon={faEnvelope} />{' '}
												</>
											}
											placeholder="Ваш актуальный e-mail"
										/>
										<MyInput
											type="tel"
											name="tel"
											widthBox="50%"
											icon={
												<>
													<FontAwesomeIcon icon={faEnvelope} />{' '}
												</>
											}
											placeholder="телефон"
										/>
									</div>
									<div className={styles.container__btn}>
										<div className={styles.btn_home_box}>
											<Button
												// className={styles.btn_home}
												type="submit"
												title={`хочу доступ к "общему делу"`}
												width="100%"
												// height="70px"

												padd="25px 0"
											/>
										</div>

										<div className={styles.checkbox_license}>
											<input type="checkbox" checked readOnly />
											<span className={styles.text_checkbox}>
												Я согласен на обработку персональных данных и получение
												рекламных материалов, и я согласен с
												<Link className={styles.link_checkbox} to="/polices">
													публичной офертой
												</Link>
											</span>
										</div>
									</div>
								</>
							}
						/>
						<BrandBar />
					</section>
				</Container>
				<Steps />
				<ModalContainer
					title={`Сегодня вам выпал счастливый шанс. Не упустите его!`}
					children={
						<>
							<MyFormRegistraion
								modal={true}
								sendEmail={sendEmail}
								sent={sent}
								goToBack={goToBack}
								children={
									<>
										<div className={styles.form_title_modal}>
											СОЗДАЙТЕ АККАУНТ В «ОБЩЕМ ДЕЛЕ» ПРЯМО СЕЙЧАС
										</div>
										<div className={styles.container__input_modal}>
											<MyInput
												width="100%"
												backgroundColor="#ededed"
												className={styles.item__input_modal}
												type="text"
												name="name"
												icon={
													<>
														<FontAwesomeIcon icon={faUserCircle} />{' '}
													</>
												}
												placeholder="Ваше имя"
											/>
											<MyInput
												width="100%"
												backgroundColor="#ededed"
												className={styles.item__input_modal}
												type="text"
												name="lastName"
												icon={
													<>
														<FontAwesomeIcon icon={faUserCircle} />{' '}
													</>
												}
												placeholder="Ваша фамилия"
											/>
											<MyInput
												width="100%"
												name="email"
												backgroundColor="#ededed"
												className={styles.item__input_modal}
												type="email"
												icon={
													<>
														<FontAwesomeIcon icon={faEnvelope} />{' '}
													</>
												}
												placeholder="Ваш актуальный e-mail"
											/>
											<MyInput
												width="100%"
												name="tel"
												backgroundColor="#ededed"
												className={styles.item__input_modal}
												type="tel"
												icon={
													<>
														<FontAwesomeIcon icon={faEnvelope} />{' '}
													</>
												}
												placeholder="Ваш актуальный телефон"
											/>
										</div>
										<div className={styles.container__btn_modal}>
											<div className={styles.btn_home_box_modal}>
												<Button
													type="submit"
													title="получить доступ"
													width="100%"
													height="70px"
													padd="0 75"
												/>
											</div>

											<div className={styles.checkbox_license_modal}>
												<input type="checkbox" checked readOnly />
												<span className={styles.text_checkbox_modal}>
													Я согласен на обработку персональных данных и
													получение рекламных материалов, и я согласен с
													<Link
														className={styles.link_checkbox_modal}
														to="/polices"
													>
														публичной офертой
													</Link>
												</span>
											</div>
										</div>
									</>
								}
							/>
						</>
					}
				/>
				<div className={styles.inside_box}>
					{' '}
					<section className={styles.done}>
						<div class={styles.container}>
							<h2 className={styles.done__heading}>Отлично! Вы справились!</h2>
							<div className={styles.done_block}>
								<div className={styles.done_block__title}>
									Добро пожаловать в «Общее дело».
								</div>
								<div className={styles.done_block__reviews}>
									<div className={styles.done_block__reviews_title}>
										Реальные отзывы от пользователей программы «Общее дело»:
									</div>
									<div className={styles.done_block__reviews_items}>
										<Slider />
									</div>
								</div>
							</div>
						</div>
					</section>
					<section className={styles.how}>
						<div className={styles.container__how}>
							<h2 className={styles.container__how_heading}>
								Как это всё работает
							</h2>
							<div className={styles.how__player}>
								{/* <!-- player('boLowLNkCl0', 'false', 'maxres'); --> */}
							</div>
						</div>
					</section>
					<TextSection />
					<section className={styles.form__wrapper}>
						<div className={styles.container__wrapper}>
							<div className={styles.row}>
								<div className={styles.col5}>
									<div className={styles.form}>
										<h5 className={styles.title}>
											1 ШАГ: Создайте учётную запись в «Общем деле».
										</h5>
										<div className={styles.form__container}>
											<MyFormRegistraion
												sendEmail={sendEmail}
												sent={sent}
												goToBack={goToBack}
												modal={true}
												backgroundColor="none"
												padd="0"
												children={
													<>
														<div className={styles.container__input_modal}>
															<MyInput
																width="100%"
																backgroundColor="#ededed"
																className={styles.item__input_modal}
																type="text"
																name="name"
																icon={
																	<>
																		<FontAwesomeIcon icon={faUserCircle} />{' '}
																	</>
																}
																placeholder="Ваше имя"
															/>
															<MyInput
																width="100%"
																backgroundColor="#ededed"
																className={styles.item__input_modal}
																type="text"
																name="lastName"
																icon={
																	<>
																		<FontAwesomeIcon icon={faUserCircle} />{' '}
																	</>
																}
																placeholder="Ваша фамилия"
															/>
															<MyInput
																width="100%"
																name="email"
																backgroundColor="#ededed"
																className={styles.item__input_modal}
																type="email"
																icon={
																	<>
																		<FontAwesomeIcon icon={faEnvelope} />{' '}
																	</>
																}
																placeholder="Ваш актуальный e-mail"
															/>
															<MyInput
																type="tel"
																name="tel"
																width="100%"
																backgroundColor="#ededed"
																icon={
																	<>
																		<FontAwesomeIcon icon={faEnvelope} />{' '}
																	</>
																}
																placeholder="телефон"
															/>
														</div>
														<div className={styles.container__btn_modal}>
															<div className={styles.btn_home_box_modal}>
																<Button
																	className={styles.down__form_btn}
																	type="submit"
																	title="получить доступ"
																	width="100%"
																	height="70px"
																	padd="0 75"
																/>
															</div>

															<div className={styles.checkbox_license_modal}>
																<input type="checkbox" checked readOnly />
																<span className={styles.text_checkbox_modal}>
																	Я согласен на обработку персональных данных и
																	получение рекламных материалов, и я согласен с
																	<Link
																		className={styles.link_checkbox_modal}
																		to="/polices"
																	>
																		публичной офертой
																	</Link>
																</span>
															</div>
														</div>
													</>
												}
											/>
										</div>

										<h5 className={styles.subtitle}>
											Зарегистрируйтесь,
											<br /> чтобы начать зарабатывать уже сейчас!
										</h5>
									</div>
								</div>
								<div className={styles.col6}>
									<h5>
										Личная помощь основателя и круглосуточная поддержка команды:
									</h5>
									Мы обеспечили круглосуточную поддержку пользователей «Общего
									дела». Вы получите необходимую помощь в любое время суток.
									Чтобы обратиться к эксперту, активируйте чат, находящийся
									справа внизу страницы.
									<br />
									<br />
									Внимание! Чат доступен только зарегистрированным
									пользователям.
								</div>
							</div>
						</div>
					</section>
				</div>

				<div classname={styles.container__brand}>
					<BrandBar foot="true" />
				</div>
				<Footer
					color="black"
					children={
						<>
							<ul className={styles.menu_footer}>
								<li className={styles.item_footer}>
									<Link className={styles.link} to="/polices">
										Политика конфиденциальности
									</Link>
								</li>
								<li className={styles.item_footer}>
									<Link className={styles.link} to="/agreement">
										Пользовательское соглашение
									</Link>
								</li>
							</ul>
						</>
					}
				/>
			</div>
		</div>
	);
};

Registration.propTypes = {};

export default Registration;
